.meter {
    box-sizing: content-box;
    height: 8px;
    margin: 5px 0 2px;
    padding: 2px;
    position: relative;
    background: #555;
    border-radius: 25px;
    box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
}

.meter > span {
    display: block;
    height: 100%;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    background: linear-gradient(#fdb279, #f87110, red);
    background: #00d0ff;
    box-shadow:
        inset 0 2px 9px #ffffff4d,
        inset 0 -2px 6px #00000066,
        0 0 9px -0.5px #0fd5cc;
    overflow: hidden;
}

.group {
    cursor: pointer;
}

.group:hover .meter > span {
    box-shadow:
        inset 0 2px 9px rgb(61 248 84 / 97%),
        inset 0 -2px 6px rgba(0, 0, 0, 0.4),
        0 0px 15px rgb(37 212 16 / 97%);
    background: #13ed2b;
    animation: example 0.4s linear;
}

@keyframes example {
    0% {
        width: 0;
    }

    100% {
        /* width: 20%; */
    }
}
